@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  @keyframes elastic-in {
    0% { transform: scale(0.96); opacity: 0 }
    100% { transform: scale(1); opacity: 1 }
  }
  @keyframes elastic-out {
    0% { transform: scale(1); opacity: 1 }
    100% { transform: scale(0.98); opacity: 0 }
  }
  .animate-elastic-in { animation: elastic-in 300ms cubic-bezier(.2,.8,.2,1) }
  .animate-elastic-out { animation: elastic-out 250ms cubic-bezier(.2,.8,.2,1) }
}

@layer components {
  .footer-links {
    @apply flex flex-col sm:flex-row items-center gap-2;
  }
}


@media (max-height: 124px) {
  .footer-links {
    flex-direction: column;
  }
}
